.filedCell {
    display: table-cell;
    min-width: 20px;
    height: 20px;
    border-color: #fff;
    border-width: 0.1px;
    font-size: 0.9em;
    border-style: outset;
    background-color: #eee;
    z-index: -1;
    cursor: crosshair;

    transition: background-color 0.05s, border-color 0.05s
}

.filedCell.hidden:hover {
    background-color: #bde2ff4b;
    border-color:#bde2ff4b;
    z-index: 2;
}

.filedCell.hidden:active {
    background-color: #6dc0ff;
    border-color:#6dc0ff;
}

.filedCell.clicked {
    border-color: #eee;
    background-color: #fff;
}

.filedHolder {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 800
}

.el-table-poper {
    z-index: 2000;
}

